const promotionEl = document.querySelector('.promotion');
const promotionToggleBtn = document.querySelector('.toggle-promotion');
let isHidePromotion = false; //프로모션이 보여지고 있니? NO!
promotionToggleBtn.addEventListener('click', function () {
  isHidePromotion = !isHidePromotion //느낌표=반대 값
  if (isHidePromotion) {
    //숨김 처리
    promotionEl.classList.add('hide');
  } else {
    //보임 처리
    promotionEl.classList.remove('hide');
  }
});

// 범위 랜덤 함수(소수점 2자리까지)
function random(min, max) {
  // `.toFixed()`를 통해 반환된 문자 데이터를,
  // `parseFloat()`을 통해 소수점을 가지는 숫자 데이터로 변환
  return parseFloat((Math.random() * (max - min) + min).toFixed(2))
}
function floatingObject(selector, delay, size) {
  gsap.to( 
    selector,  //선택자 
    random(1.5, 2.5),   //애니메이션 동작 시간
    { //옵션
      y: size,
      repeat: -1, //무한 반복
      yoyo: true, //한번 재생된 애니메이션을 다시 뒤로 재생시킴
      ease: Power1.easeInOut,
      delay: random(0, delay)
    }
  );
}
floatingObject('.floating1', 1, 15); //delay 1초, 위아래로 움직이는 범위 15px
floatingObject('.floating2', .5, 15); 
floatingObject('.floating3', 1.5, 20); 

// ScrollMagic
const spyEls = document.querySelectorAll('section.scroll-spy');
spyEls.forEach(function (spyEl) {
  new ScrollMagic      
    .Scene({             //Scene=스크롤매직을 통해 특정한 요소를 감시하는 옵션 지정
      triggerElement: spyEl, //보여짐 여부를 감시할 요소를 지정
      triggerHook: .8 //감시하는 요소가 뷰포트에서 반응이 일어날 지점을 지정
    })
    .setClassToggle(spyEl, 'show') //(토글한 요소 지정, 토글할 요소의 이름)
    .addTo(new ScrollMagic.Controller()); 
});

const thisYear =  document.querySelector('.this-year');
